				"html": "<h1>Java Basic Operations</h1>\n<p>Java supports the following operations on variables:</p>\n<ul>\n<li>Arithmetic : <code>Addition(+)</code>, <code>Subtraction(-)</code>, <code>Multiplication(*)</code>, <code>Division(/)</code>, <code>Modulus(%)</code>,<code>Increment(++)</code>,<code>Decrement(--)</code>.</li>\n<li>String concatenation: <code>+</code> can be used for String concatenation but subtraction <code>-</code> on a String is not a valid operation.</li>\n<li>Relational: <code>Equal to(==)</code>, <code>Not Equal to (!=)</code>, <code>Greater than(>)</code>, <code>Less than(&#x3C;)</code>, <code>Greater than or equal to(>=)</code>, <code>Less than or equal to(&#x3C;=)</code>,</li>\n<li>Bitwise: <code>Bitwise And(&#x26;)</code>, <code>Bitwise Or(|)</code>, <code>Bitwise XOR(^)</code>, <code>Bitwise Compliment(~)</code>, <code>Left shift(&#x3C;&#x3C;)</code>, <code>Right Shift (>>)</code>, <code>Zero fill right shift (>>>)</code></li>\n<li>Logical: <code>Logical And (&#x26;&#x26;)</code>, <code>Logical Or(||)</code>, <code>Logical Not (!)</code></li>\n<li>Assignment: <code>=</code>, <code>+=</code>, <code>-=</code>, <code>*=</code>, <code>/=</code>, <code>%=</code>, <code>&#x3C;&#x3C;=</code>, <code>>>=</code>, <code>&#x26;=</code>, <code>^=</code>, <code>|=</code></li>\n<li>Others: <code>Conditional/Ternary(?:)</code>, <code>instanceof</code></li>\n</ul>\n<p>While most of the operations are self explanatory, the Conditional (Ternary) Operator works as follows:</p>\n<p><code>expression that results in boolean output ? return this value if true : return this value if false</code></p>\n<p>For e.g:</p>\n<pre><code>int x = 10;\nint y = (x == 10) ? 5 : 9; &#x3C;-- y will equal 5 since the expression x == 10 evaluates to true\n</code></pre>",
